<template>
  <div class="member-box">
      <div class="query-wrapper">
        <div>
          <el-form :inline="true" :model="queryForm" ref="queryForm" label-width="100px">
            <el-form-item label="手机号">
              <el-input v-model="queryForm.phone" :placeholder="'input' | solvePlaceholder" />
            </el-form-item>
            <el-form-item label="录入人">
              <el-input v-model="queryForm.enterMan" :placeholder="'input' | solvePlaceholder" />
            </el-form-item>
            <el-form-item label="客户姓名">
              <el-input v-model="queryForm.userName" :placeholder="'input' | solvePlaceholder" />
            </el-form-item>
            <el-form-item label="客户维度">
              <el-select v-model="queryForm.customerDimension">
                <el-option label="全部" value="1" />
                <el-option label="今日新增" value="2" />
                <el-option label="从未跟进" value="3" />
              </el-select>
            </el-form-item>
          </el-form>
        </div>
        <div class="query-btn-group mt15">
          <el-button type="primary" @click="query">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </div>
      </div>
      <div class="table-box">
        <el-row class="table-header" type="flex" justify="space-between">
          <el-col :span="6" class="customer-num">共选择了15个客户</el-col>
          <el-col :span="18" class="table-header-btn-group">
            <el-dropdown class="dropdown mr15" v-for="(item, index) in tableHeaderBtnGroups" :key="item | solveKey">
              <el-button type="primary">{{item.title}}</el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="(cItem) in item.children">
                  <span style="display:block;" @click="opera(item.type, cItem)">{{item.title}}{{cItem.title}}</span>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>
        </el-row>
        <el-table
          class="w100"
          ref="customerTable"
          :data="customers"
          tooltip-effect="dark"
        >
          <el-table-column type="selection" width="55" />
          <el-table-column  label="客户名称">
            <template slot-scope="scope">
              <span @click="lookRelaRems" class="cur hover-active" style="text-decoration: underline;">{{scope.row.userName}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="userName1" label="手机号" />
          <el-table-column prop="userName2" label="意向额度(万元)" />
          <el-table-column prop="userName3" label="所在城市" />
          <el-table-column prop="userName4" label="客户状态" />
          <el-table-column prop="userName5" label="最近跟进时间" />
          <el-table-column prop="userName6" label="写跟进" />
        </el-table>

        <el-pagination
          style="text-align: center;margin-top: 15px"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400">
        </el-pagination>
      </div>
      <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible">
        <el-table :data="follows" class="w100">
          <el-table-column prop="userName" label="跟进人" />
          <el-table-column prop="userName1" label="跟进时间" />
          <el-table-column prop="userName12" label="跟进结果" />
        </el-table>
        <span slot="footer">
          <el-button @click="e=>dialogFormVisible=false">取消</el-button>
        </span>
      </el-dialog>
      <el-dialog title="新增跟进记录" :visible.sync="dialogFollowVisible">
        <el-form :model="followForm" ref="followForm">
          <el-form-item label="客户名称">
            <el-input :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
          <el-form-item label="跟进人">
            <el-input :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
          <el-form-item label="跟进结果">
            <el-input :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
        </el-form>

        <span slot="footer">
          <el-button type="primary">保存</el-button>
          <el-button @click="e=>dialogFollowVisible=false">取消</el-button>
        </span>
      </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'Customer',
    data() {
      return {
        queryForm: {},
        tableHeaderBtnGroups: [
          {title:'创建', type: '1', children: [{title: '客户', name: 'Customer'},{title: '跟进记录', name: 'Follow'}]},
          {title:'转交', type: '2', children: [{title: '负责人', name: 'Director'},{title: '公共客户', name: 'Public'}]},
          {title:'导出', type: '3', children: [{title: '所选客户', name: 'SelectedCustomer'},{title: '当前页客户', name: 'CurrentCustomer'}]}
        ],
        customers: [],
        currentPage4: null,
        dialogFormVisible: false,
        dialogTitle: '',
        follows:[{userName:'员工1',userName1:'2022-4-22',userName2:'客户考虑中'}],
        dialogFollowVisible: false,
        followForm: {}
      }
    },
    created() {
      let _this = this
      let _routeName = _this.$route.name
      if (_routeName == 'PublicCustomer') {
        _this.customers = [
          {userName:'公共客户',userName1:'123456',userName2:'133',userName3:'浙江杭州',userName4:'意向客户',userName5:'2022-4-11',userName6:'写跟进'},
        ]
      } else if(_routeName == 'PersonalCustomer') {
        _this.customers = [
          {userName:'个人客户',userName1:'123456',userName2:'133',userName3:'浙江杭州',userName4:'意向客户',userName5:'2022-4-11',userName6:'写跟进'},
        ]
      } else {
        _this.customers = [
          {userName:'个人客户',userName1:'123456',userName2:'133',userName3:'浙江杭州',userName4:'意向客户',userName5:'2022-4-11',userName6:'写跟进'},
          {userName:'公共客户',userName1:'123456',userName2:'133',userName3:'浙江杭州',userName4:'意向客户',userName5:'2022-4-11',userName6:'写跟进'},
          {userName:'客户A',userName1:'123456',userName2:'133',userName3:'浙江杭州',userName4:'意向客户',userName5:'2022-4-11',userName6:'写跟进'}
        ]
      }
    },
    methods: {
      query() {},
      reset() {},
      handleSizeChange() {},
      handleCurrentChange() {},
      lookRelaRems() {
        let _this = this
        _this.dialogFormVisible = true
        _this.dialogTitle = `客户A的跟进记录`
      },
      opera(operaType, detail) {
        let _this = this
        if (operaType == '1') {
          if (detail.name == 'Follow') {
            _this.dialogFollowVisible = true
          } else if(detail.name == 'Customer') {
            _this.$router.push({path: '/entercustomer'})
          }
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  .member-box {
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    .query-btn-group {
      text-align: right;
    }
    .table-header{
      margin: 30px 0 15px;
      .table-header-btn-group{
        text-align: right;
        .dropdown:last-child{
          margin-right: 0;
        }
      }
    }
    .hover-active{
      color: #3a8ee6;
    }
  }
</style>
